/* main_banner */
.main_banner {
    width: 100%;
}
.swiper {
    background-color: #f5f5f5;
}
.swiper__item {
    position: relative;
    background-color: #f5f5f5;
    background-size: cover;
}
.swiper__item::before{
    content: '';
    display: block;
    padding-top: 100%;
}
.content__wrap {
    position: absolute;
    top: 0;
    left:0;
    width: 100%;
    height: 100%;
}
.state_message {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: #999;
}

/* Swiper 페이지네이션 커스텀 */
.main_banner :global(.swiper-pagination.swiper-pagination-progressbar.swiper-pagination-horizontal) {
    width: calc(100% - 0.4rem);
    top: unset;
    bottom: 0.35rem !important;
    left: 0.2rem;
    border-radius: 0.2rem;
    background-color: rgba(255,255,255,0.3);
}
.main_banner :global(.swiper-pagination-progressbar .swiper-pagination-progressbar-fill) {
    background-color: #fff;
    border-radius: 0.2rem;
}